.banner {
  height: 70vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: url("https://firebasestorage.googleapis.com/v0/b/soyjuanjohernandez-blog.appspot.com/o/background_banner1.jpg?alt=media&token=a17a85a4-6818-4502-8049-0dd077d686f6")
    no-repeat;
  background-size: cover;
}

.banner img {
  width: 400px;
}

@media (max-width: 900px) {
  .banner {
    height: 60vh;
    background-position-x: center;
  }
  .banner img {
    width: 300px;
  }
}
