.project-card {
  position: relative;
  width: 331px;
  height: 373px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border-radius: 10px;
  background-color: var(--color-primary);
  box-shadow: var(--box-shadow);
  box-sizing: border-box;
  overflow: hidden;
  transition: all 0.3s;
  position: relative;
}

.project-card:hover {
  box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.719);
}

.project-card h3 {
 margin-left: 10px;
}

.project-card__techs {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 10px;
}

.project-card__logo-tech {
  height: 35px;
  width: auto;
}

.project-card__info{
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: var(--color-bg-info-card);
  backdrop-filter: blur(2px);
  display: flex;
  flex-direction: column;
  align-items: center;
  bottom: -369px;
  transition: all .1s;
}

.project-card__info p{
 width: 80%;
 text-align: start;
 margin-top: 20px;
}

.project-card__info a{
  position: absolute;
  bottom: 20px;
  cursor: pointer;
}

.project-card:hover .project-card__info{
  bottom: 0;
  transition: all .1s;
}